.clock {
    height: 500px;
    width: 500px;
    border-radius: 250px;
    display: flex;
    justify-content: center;
    position: relative;
}
.clock__second, .clock__minute, .clock__hour, .clock__indicator {
    position: absolute;
    left: calc(50% - 1px);
    width: 2px;
    background: #f4eed7;
    transform-origin: bottom center;
    z-index: 2;
    border-radius: 1px;
}
.clock__second {
    height: 240px;
    margin-top: 10px;
    background: #26574B;
    animation: time 60s infinite steps(60);
    z-index: 3;
}
.clock__minute {
    height: 230px;
    margin-top: 20px;
    opacity: 0.75;
    animation: time 3600s linear infinite;
}
.clock__hour {
    height: 210px;
    margin-top: 40px;
    animation: time 43200s linear infinite;
}
.clock__indicator {
    height: 248px;
    border-top: 0px solid #4b9aaa;
    background: none;
}
.clock__indicator:nth-of-type(5n) {
    opacity: 1;
    height: 243px;
    border-top: 7px solid #f4eed7;
}
.clock__axis {
    background: #4b9aaa;
    width: 5px;
    height: 5px;
    border-radius: 3px;
    position: absolute;
    z-index: 4;
    top: 247px;
}
section:nth-of-type(1) {
    transform: rotateZ(calc(6deg * 1));
}
