@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:200,400,700);
html, body{
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    font-family: 'Roboto Mono', cursive;
    font-size:12px;
}

.footer {
    /*background: "#26574b",*/
    color: #ffffff;
    padding: 20;
    position: absolute;
    top: 95%;
    left: 50%;
    transform: translate(-50%,-100%);
}

a {
    text-decoration:none;
    color: #ffffff;
    font-size: 18px;
}

.top-left-frame {
    border: 0px solid rgba(13, 33, 48, 1);
    background-color: rgba(255, 255, 255, 0.20);
    border-radius: 0 0 0 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.top-left {
    top: 0px; 
    left: 0px; 
    width: 165px; 
    position: fixed; 
    margin-left: 80px; 
    z-index: 50;
    height: 208px;
}

.top-left-box {
    left: 22px; 
    width: 68px; 
    position: absolute; 
    top: 77px; 
    height: 68px;
}

.top-left-box-line {
    stroke-width: 0px; 
    fill-opacity: 1; 
    stroke: rgb(13, 33, 48); 
    stroke-opacity: 1; 
    fill: rgb(255, 255, 255);
}

.top-left-word {
    left: 42px; 
    width: 173px; 
    position: absolute; 
    pointer-events: auto; 
    top: 90px;
}

.block{
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  font-size: 16px;
}

.block h1{
    position: relative;
    display: block;
    text-align: center;
    margin: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: 10vw;
    color: white;
    font-weight: 400;
}

.block h2{
    position: relative;
    display: block;
    text-align: center;
    margin: 0;
    top: 43%;
    transform: translateY(-50%);
    font-size: 24px;
    color: white;
    font-weight: 200;
}

.block h3{
    position: relative;
    display: block;
    text-align: center;
    margin: 0;
    top: 45%;
    transform: translateY(-50%);
    font-size: 20px;
    color: white;
    font-weight: 200;
}

.block h4{
    position: relative;
    display: block;
    text-align: center;
    margin: 0;
    top: 49%;
    transform: translateY(-50%);
    font-size: 22px;
    color: white;
    font-weight: 200;
}

.block .blockclock {
    display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
    margin: 0;
    top: 50%;
    font-size: 10vw;
    color: white;
    font-weight: 400;
}

.img-parallax {
  width: 100vmax;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%,0);
  pointer-events: none
}
.clock {
    height: 500px;
    width: 500px;
    border-radius: 250px;
    display: flex;
    justify-content: center;
    position: relative;
}
.clock__second, .clock__minute, .clock__hour, .clock__indicator {
    position: absolute;
    left: calc(50% - 1px);
    width: 2px;
    background: #f4eed7;
    transform-origin: bottom center;
    z-index: 2;
    border-radius: 1px;
}
.clock__second {
    height: 240px;
    margin-top: 10px;
    background: #26574B;
    animation: time 60s infinite steps(60);
    z-index: 3;
}
.clock__minute {
    height: 230px;
    margin-top: 20px;
    opacity: 0.75;
    animation: time 3600s linear infinite;
}
.clock__hour {
    height: 210px;
    margin-top: 40px;
    animation: time 43200s linear infinite;
}
.clock__indicator {
    height: 248px;
    border-top: 0px solid #4b9aaa;
    background: none;
}
.clock__indicator:nth-of-type(5n) {
    opacity: 1;
    height: 243px;
    border-top: 7px solid #f4eed7;
}
.clock__axis {
    background: #4b9aaa;
    width: 5px;
    height: 5px;
    border-radius: 3px;
    position: absolute;
    z-index: 4;
    top: 247px;
}
section:nth-of-type(1) {
    transform: rotateZ(calc(6deg * 1));
